"use client"

import { useState, useEffect, useMemo } from "react"
import { Card } from "@/components/ui/card"
import { BookOpen, Trophy, Calendar, Hash, Loader2, Star } from "lucide-react"
import { getReadingData } from "@/lib/api-client"

// Nomes dos meses para exibição
const MONTH_NAMES = [
  "Janeiro", "Fevereiro", "Março", "Abril", "Maio", "Junho",
  "Julho", "Agosto", "Setembro", "Outubro", "Novembro", "Dezembro"
];

export default function YearlyReview({ userEmail }: { userEmail: string }) {
  const [allBooks, setAllBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const currentYear = new Date().getFullYear()

  useEffect(() => {
    async function loadAll() {
      if (!userEmail) return
      try {
        setLoading(true)
        const data: any = await getReadingData(userEmail, currentYear)
        const booksArray = Array.isArray(data) ? data : (data?.data || [])
        // Filtra apenas os que não estão com status 'lendo'
        setAllBooks(booksArray.filter((b: any) => b.status !== 'lendo'))
      } catch (err) {
        console.error(err)
      } finally {
        setLoading(false)
      }
    }
    loadAll()
  }, [userEmail, currentYear])

  const yearlyStats = useMemo(() => {
    if (allBooks.length === 0) return null

    // 1. Total de páginas
    const totalPages = allBooks.reduce((acc, b) => acc + (Number(b.total_pages) || 0), 0)

    // 2. Achar o mês que mais leu
    const monthCounts = allBooks.reduce((acc: any, book) => {
      const m = Number(book.month) - 1;
      acc[m] = (acc[m] || 0) + 1;
      return acc;
    }, {});

    let bestMonthIdx = 0;
    let maxBooks = 0;
    Object.entries(monthCounts).forEach(([idx, count]: any) => {
      if (count > maxBooks) {
        maxBooks = count;
        bestMonthIdx = Number(idx);
      }
    });

    return {
      totalBooks: allBooks.length,
      totalPages,
      bestMonth: MONTH_NAMES[bestMonthIdx],
      bestMonthCount: maxBooks
    }
  }, [allBooks])

  if (loading) return <div className="flex justify-center p-20"><Loader2 className="animate-spin text-primary" /></div>

  return (
    <div className="max-w-6xl mx-auto p-6 space-y-8">
      <header className="text-center space-y-2">
        <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-800">Retrospectiva {currentYear}</h1>
        <p className="text-slate-400 font-bold uppercase text-[10px] tracking-widest">Sua jornada literária completa</p>
      </header>

      {/* QUADROS DE DESTAQUE */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="p-6 bg-primary text-white border-none flex flex-col items-center justify-center text-center">
          <BookOpen className="mb-2 opacity-50" size={32} />
          <p className="text-4xl font-black">{yearlyStats?.totalBooks || 0}</p>
          <p className="text-[10px] font-bold uppercase tracking-widest">Livros Lidos</p>
        </Card>

        <Card className="p-6 bg-slate-800 text-white border-none flex flex-col items-center justify-center text-center">
          <Hash className="mb-2 opacity-50" size={32} />
          <p className="text-4xl font-black">{yearlyStats?.totalPages.toLocaleString() || 0}</p>
          <p className="text-[10px] font-bold uppercase tracking-widest">Páginas Lidas</p>
        </Card>

        <Card className="p-6 bg-amber-500 text-white border-none flex flex-col items-center justify-center text-center">
          <Trophy className="mb-2 opacity-50" size={32} />
          <p className="text-2xl font-black uppercase">{yearlyStats?.bestMonth || "---"}</p>
          <p className="text-[10px] font-bold uppercase tracking-widest">Mês que mais leu ({yearlyStats?.bestMonthCount || 0} livros)</p>
        </Card>
      </div>

      {/* GALERIA DE CAPAS */}
      <div className="space-y-4">
        <h2 className="text-sm font-black uppercase text-slate-500 flex items-center gap-2">
          <Calendar size={16} /> Estante do Ano
        </h2>
        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4">
          {allBooks.map((book, i) => (
            <div key={i} className="group relative">
              <div className="aspect-[2/3] rounded-lg overflow-hidden shadow-md bg-slate-100 border border-slate-200">
                <img 
                  src={book.cover_url || "https://via.placeholder.com/300x450"} 
                  className="w-full h-full object-cover transition-transform group-hover:scale-105" 
                  alt={book.book_name} 
                />
              </div>
              <div className="mt-2">
                <p className="text-[9px] font-black uppercase text-slate-700 truncate">{book.book_name}</p>
                <div className="flex gap-0.5 mt-0.5">
                  {[...Array(5)].map((_, i) => (
                    <Star key={i} size={8} className={i < (book.rating || 0) ? "fill-amber-400 text-amber-400" : "text-slate-200"} />
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}